@import "/src/styles/variables"
.this_day_info
  position: relative
  padding: 40px
  max-width: 750px
  width: 100%
  background: var(#{--components-background-default})
  box-shadow: $box-shadow
  border-radius: $border-radius

.item
  position: relative
  display: flex
  align-items: center
  margin-bottom: 25px
  &:last-of-type
    margin: 0
  .indicator, .indicator_name
    margin-right: 20px
  .indicator
    width: 38px
    height: 38px
    display: flex
    align-items: center
    justify-content: center
    background: $light-background
    box-shadow: 1px 4px 10px -1px rgba(71,137, 255, 0.2)
    border-radius: 50%
  .indicator_name,.indicator_value
    font-size: 14px
  .indicator_name
    font-size: 14px
    color: $base-color
  .indicator_value
    font-size: 14px
    color: var(#{--text-color-default})

.cloud_img
  position: absolute
  top: 0
  right: 0